}

/* Webfont: Mak-Light */@font-face {
  font-family: 'MakLight';
  src: url(/static/media/MAK.e0b9ae55.eot); /* IE9 Compat Modes */
  src: url(/static/media/MAK.e0b9ae55.eot?#iefix) format('embedded-opentype'), 
       url(/static/media/MAK.68a897ff.woff) format('woff'), 
       url(/static/media/MAK.8e809e3d.otf) format('truetype'); /* Safari, Android, iOS */
           font-style: normal;
  font-weight: normal;
  text-rendering: optimizeLegibility;
}
