}


/* Menu */

Link, a {
  font-family: 'MakLight';
  color: white;
  -webkit-text-decoration-line: none;
          text-decoration-line: none;
}

a.normalLinks{
  text-decoration: underline;
  color: #333;
  font-family: 'Roboto';
}


.burger-menu,
.burger-menu.open {
  display: inline-block;
  cursor: pointer;
  position: fixed;
  right: 20px;
  bottom: 40px;
  z-index: 9999;
  background: #fff;
  padding: 10px;
  border-radius: 25px;
  box-shadow: 0 1px 4px rgba(0, 0, 0, 0.3);
}

.burger-menu .bar1,
.bar2,
.bar3 {
  width: 25px;
  height: 3px;
  background-color: #333;
  margin: 4px 0;
  transition: 0.4s;
}

.burger-menu.open .bar1 {
  transform: rotate(-45deg) translate(-4px, 4px);
}

.burger-menu.open .bar2 {
  opacity: 0;
}

.burger-menu.open .bar3 {
  transform: rotate(45deg) translate(-6px, -6px);
}



.menu {
  width: 100%;
  display: block;
  text-align: center;
  padding: 0px;
}
.menu ul {
  position: relative;
  top: 0px;
  font-size: 24px;
  padding: 0px;
}
.menu li {
  list-style: outside none none;
  margin: 10px 0px;
  padding: 0;
  cursor: pointer;
  font-family: 'MakLight';
  text-transform: lowercase;
  font-size: 3rem;
  line-height: 5rem;
  color: white;
}
.menu li:hover {
  color: #ff0000;
}


.social-icons{
